import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { Appearance } from "@rp/react/rpjson/types"
import { RpJsonDocument } from "@rp/react/rpjson/types"
import { ShowMoreList } from "@rp/react/web/common/composite-components"
import { MentionHeader, useAppereances } from "@rp/react/web/common/discovery-sidebar"
import { Typography, capitalize } from "@rp/react/web/mui/core"
import { DetectionCard } from "./DetectionCard"
import { DetectionCardList, DetectionContainer } from "./Detections.styles"

type Props = {
  rpjsonDocument: RpJsonDocument
  filter: CqsItem
  onClose: () => void
  selectedDetection?: { pnum: number; snum: number }
  onDetectionClick: (appereance: Appearance) => void
  disableCheckbox?: boolean
  onChange?: (item: CqsItem) => void
  onExclude?: (item: CqsItem) => void
  title: string
  onAddToWatchlist?: (watchlist: Watchlist) => void
}

export const Detections = ({
  rpjsonDocument,
  filter,
  onClose,
  onChange,
  selectedDetection,
  onDetectionClick,
  disableCheckbox = false,
  title,
  onAddToWatchlist,
  onExclude,
}: Props) => {
  const appereances: Array<Appearance> = useAppereances(filter, rpjsonDocument)
  return (
    <DetectionContainer>
      <MentionHeader
        onClose={onClose}
        backButtonTitle={`Back to ${capitalize(title)}`}
        item={filter}
        onChange={disableCheckbox ? undefined : onChange}
        onAddToWatchlist={onAddToWatchlist}
        onExclude={onExclude}
      />
      <Typography variant="caption" sx={{ color: "grey.200", pl: 1.5 }}>
        Showing {appereances.length} mentions
      </Typography>
      <DetectionCardList>
        <ShowMoreList<Appearance>
          renderItem={det => {
            const detection = { ...det, key: filter.key, queryType: filter.queryType }
            return (
              <DetectionCard
                key={`${det.pnum}-${det.snum}`}
                rpjsonDocument={rpjsonDocument}
                detection={detection}
                selected={
                  det.pnum === selectedDetection?.pnum && det.snum === selectedDetection?.snum
                }
                onClick={() => {
                  onDetectionClick(det)
                }}
              />
            )
          }}
          items={appereances}
        />
      </DetectionCardList>
    </DetectionContainer>
  )
}
